var Preview = require('../components/preview.html');
var NotFound = require('../components/404.html');

module.exports = function() {
  if (app.state.get(['community', 'headlessModeEnabled']) && !app.state.get(['community', 'externalPreviewEnabled'])) {
    app.render(NotFound);
  } else {
    app.render(Preview);
  }
};
