function AmpValidationError(invalidObjects) {
  Error.call(this);

  var message = 'AMP validation error. Some story objects will block publishing on AMP.';
  if (typeof Error.captureStackTrace === 'function') {
    Error.captureStackTrace(this, AmpValidationError);
  } else {
    this.stack = (new Error(message)).stack;
  }

  this.message = message;
  this.invalidObjects = invalidObjects;
  this.name = this.constructor.name;
}

AmpValidationError.prototype = Object.create(Error.prototype);
AmpValidationError.prototype.constructor = AmpValidationError;

export default AmpValidationError;
