var { getEntryParams } = require('../../actions/docs');
var toPublicHtml = require('convert-rich-text').toPublicHtml;
var hash = require('../../../shared/content-hash');
var insertModifyButtons = require('./insert_modify_buttons');
var modifyLinks = require('./modify_links');
var request = require('../request');
var isEmpty = require('../../../shared/delta/is_empty');
var isPackage = require('../../../shared/is_package');

const CACHE = new Map();

export default function renderBody(doc, contents) {
  var body = doc.querySelector('.c-entry-content');
  // audience-facing landing pages for packages render additional markup
  // that isn't stored in anthem in any way. to preserve that markup,
  // do not overwrite the innerHTML with anthem-only doc.head.contents
  if (!isPackage(contents)) {
    body.innerHTML = toBodyExtended(contents);
  }

  var els = Array.from(body.querySelectorAll('[data-anthem-component]'));
  var promise;

  if (els.every(el => CACHE.has(cacheKey(el)))) {
    // don't need to fetch entry params if every anthem component's markup is already cached
    promise = Promise.all(els.map(el => renderAnthemComponent(el, null)));
  } else {
    promise = getEntryParams(app.state.get(['doc', 'id'])).then(function(entryParams) {
      return Promise.all(els.map(el => {
        return renderAnthemComponent(el, getAnthemComponentParams(el, entryParams));
      }));
    });
  }

  return promise.then(() => {
    return modifyLinks(doc);
  }).then(() => {
    if (
      app.state.get(['community', 'adsEnabled']) &&
      !app.state.get(['doc', 'head', 'contents', 'draft', 'disable_ads'])
    ) {
      return insertAds(body);
    }
  }).then(() => {
    return insertModifyButtons(body);
  });
}

function toBodyExtended(contents) {
  var body = toPublicHtml(contents.draft.body);
  var corrections = (contents.draft.corrections || []).reduce((params, correction) => {
    if (!isEmpty(correction.body)) {
      params.push({
        date: correction.date || new Date().toISOString(),
        body: toPublicHtml(correction.body)
      });
    }
    return params;
  }, []);

  if (corrections.length > 0) {
    body += `<div data-anthem-component="correction" data-anthem-component-data='${JSON.stringify({ corrections })}'></div>`;
  }

  return body;
}

function insertAds(container) {
  try {
    container.ownerDocument.defaultView.ConcertAds.reinstall(container);
  } catch (e) {
    // ignore error
  }
}

function getAnthemComponentParams(el, entryParams) {
  var anthemComponent = el.dataset.anthemComponent;
  var subdocComponentParams = entryParams.anthem_components.find((params) => {
    return params.anthem_id === anthemComponent;
  });

  var anthemComponentDataParams = el.dataset.anthemComponentData ? { data: JSON.parse(el.dataset.anthemComponentData) } : {};
  if (!subdocComponentParams) {
    return anthemComponentDataParams;
  }
  // if a subdoc also has placement data params
  if (anthemComponentDataParams.data) {
    subdocComponentParams.data = { ...subdocComponentParams.data, ...anthemComponentDataParams.data };
  }
  return subdocComponentParams;
}

function renderAnthemComponent(el, params) {
  return getAnthemComponentHtml(el, params).then(function(html) {
    var div = el.ownerDocument.createElement('div');
    div.innerHTML = html;
    // Make all data-cid attributes unique by adding a timestamp,
    // so that presto re-initializes them correctly
    for (var component of [...div.querySelectorAll('[data-cid]')]) {
      component.dataset.cid += `-${Date.now()}`;
    }
    // scripts wont execute when added via innerHTML, so gotta strip
    // them out and add them back in
    for (var script of [...div.querySelectorAll('script')]) {
      if (script.type === 'text/javascript') {
        var src = script.src;
        var parent = script.parentNode;
        parent.removeChild(script);
        var newscript = el.ownerDocument.createElement('script');
        newscript.src = src;
        parent.appendChild(newscript);
      }
    }
    while (div.firstChild) {
      el.parentNode.insertBefore(div.firstChild, el);
    }
    el.parentNode.removeChild(el);
  });
}

function getAnthemComponentHtml(el, params) {
  var key = cacheKey(el);
  if (CACHE.has(key)) {
    return Promise.resolve(CACHE.get(key));
  }

  if (!params) {
    let err = new Error('anthem component params not found');
    err.anthem_component = el.dataset.anthemComponent;
    return Promise.reject(err);
  }

  var [type] = el.dataset.anthemComponent.split(':');
  if (type === 'correction' || type === 'ratingcard') {
    params.data.entry_id = app.state.get(['doc', 'entry_id']);
  }
  return request(`/api/chorus/cell/unison_anthem_component/${type}`, {
    method: 'POST',
    headers: { Accept: 'text/html' },
    body: { data: params.data, anthem_id: el.dataset.anthemComponent },
    query: { community_id: app.state.get(['community', 'id']) }
  }).then(function(html) {
    CACHE.set(key, html);
    return html;
  });
}

function cacheKey(el) {
  var anthemComponent = el.dataset.anthemComponent;
  if ((anthemComponent.indexOf(':') > -1) && !el.dataset.anthemComponentData) {
    return hash(anthemComponent);
  }
  return hash(`${anthemComponent}:${el.dataset.anthemComponentData}`);
}
