import { forOwn } from 'lodash';
import { toPlaintext } from 'convert-rich-text';
import request from '../lib/request';
import iframeReady from '../lib/iframe_ready';
import renderBody from '../lib/layout/render_body';
import { getAllDocWarnings, hasStatus } from '../lib/partner_platforms';
import AmpValidationError from '../lib/errors/amp_validation_error';
import { PreviewQuery } from '../graphql/queries/preview';
import createHmac from 'create-hmac';
import graphqlRequest from '../lib/graphql_request';

export const INCREMENTAL = [
  'draft.hed',
  'draft.body',
  'draft.styles',
];
export const IGNORED = [
  'draft.slug',
  'draft.autotweets',
  'draft.publish_scheduled_for',
  'draft.teaser',
  'draft.internal_image',
  'draft.internal_title',
  'draft.internal_description',
  'draft.social_image',
  'draft.social_title',
  'draft.social_description',
  'draft.seo_image',
  'draft.seo_title',
  'draft.seo_description',
  'publish_scheduled_for',
  'publish_history',
  'image_history',
  'embargoed',
  'published',
  'plagscan'
];

export function getPreviewHTML(amp = false, layoutMode = false) {
  // Get external preview if it's enabled, and we're not in layout mode.
  if (app.state.get(['community', 'externalPreviewEnabled']) && app.state.get(['community', 'externalPreviewMode']) === 'default' && !layoutMode) {
    return getExternalPreviewMarkup(amp);
  }

  var validationPromise = amp ? validateForAmp() : Promise.resolve();
  return validationPromise
    .then(() => {
      return Promise.all([
        getPreviewMarkup(amp),
        getPreviewStyles()
      ]);
    })
    .then(([html, scss]) => parsePreviewMarkup(html, scss));
}

function validateForAmp() {
  var blocked = getAllDocWarnings(app.state.get(['doc'])).reduce((objects, warning) => {
    if (hasStatus(warning.message, 'BLOCK', 'Google AMP')) {
      objects.push(warning.object);
    }
    return objects;
  }, []);

  if (blocked.length > 0) {
    var error = new AmpValidationError(blocked);
    return Promise.reject(error);
  }

  return Promise.resolve();
}

async function getPreviewMarkup(amp) {
  const { anthemStory } = await graphqlRequest('/api/tower/graphql', {
    query: PreviewQuery,
    variables: {
      uuid: app.state.get(['doc', 'uuid']),
      platform: amp ? 'AMP' : 'HTML'
    }
  }, { cache: false });

  return anthemStory.previewHtml;
}

async function getExternalPreviewMarkup(amp) {
  var query = { uuid: app.state.get(['doc', 'uuid']), user_id: app.state.get(['user', 'id']) };
  if (amp) { query.platform = 'AMP'; }
  query = Object.keys(query).map(key => key + '=' + query[key]).join('&');
  var url = [app.state.get(['community', 'externalPreviewUrl']), query].join('?');
  var requestMethod = app.state.get(['community', 'externalPreviewRequestMethod']);
  var additionalHeaders = app.state.get(['community', 'externalPreviewApiHeaders']);
  var requestHeaders = {
    'Accept': 'text/html',
    'X-Chorus-Signature': sign(query)
  };

  if (additionalHeaders) {
    requestHeaders = Object.assign(requestHeaders, additionalHeaders);
  }

  var { data } = await request(url, {
    method: requestMethod,
    credentials: 'omit',
    headers: requestHeaders,
    opts: {
      withHeaders: true
    }
  });
  return data;
}

function sign(message) {
  var sharedSecret = app.state.get(['community', 'externalPreviewSharedSecret']);
  return createHmac('sha256', sharedSecret).update(message).digest('base64');
}

function parsePreviewMarkup(html, scss) {
  var stylesheetUrl = app.config.publicPath === '/' ?
    [window.location.origin, window.cssManifest['preview.css']].join('') :
    window.cssManifest['preview.css'];

  return html
    // insert rendered scss
    .replace(/<style id="custom-entry-styles"(.|\n)*?<\/style>/, `<style id="custom-entry-styles" type="text/css">${scss}</style>`)
    // https://stackoverflow.com/questions/26694141/svgs-inside-iframes-having-strange-inconsistency-problems/26704018#comment59524773_26704018
    .replace(/xlink:href="/g, 'xlink:href="about:srcdoc')
    // All links should open outside the iframe
    .replace(/<a\s/g, '<a target="_blank"')
    .replace(/<\/head>/, `<link rel="stylesheet" href="${stylesheetUrl}" /></head>`)
    .replace(/(<iframe[^>]*src="[^"]+twitch\.tv[^"#]+)/g, `$1&amp;parent=${window.top.location.hostname}`)
    .replace(/(<amp-iframe[^>]*src="[^"]+twitch\.tv[^"#]+)/g, `$1&amp;parent=${window.top.location.hostname}`);
}

function getPreviewStyles() {
  var styles = app.state.get(['doc', 'head', 'contents', 'draft', 'styles']);
  if (styles) {
    return request('/api/sass', {
      method: 'POST',
      body: { input: styles }
    }).then(response => {
      return response.output;
    });
  }
  return Promise.resolve('');
}

export function updateIframe(iframe) {
  return iframeReady(iframe)
    .then(() => getPreviewStyles())
    .then(scss => {
      var doc = iframe.contentDocument;
      var contents = app.state.get(['doc', 'head', 'contents']);
      var hed = contents.draft.hed;

      forOwn({
        // TODO: inline image credits (depends on api calls)
        '.c-page-title': toPlaintext(hed),
        '#custom-entry-styles': scss
      }, (html, selector) => {
        var el = doc.querySelector(selector);
        if (el) {
          el.innerHTML = html;
        } else {
          console.warn('In layout iframe, could not find element with selector:', selector);
        }
      });

      return renderBody(doc, contents).then(() => {
        doc.dispatchEvent(new Event('anthem.preview-updated'));
      });
    });
}

export function getPrestoPreview(component, view, data) {
  return request(`/api/chorus/presto/${component}`, {
    method: 'POST',
    headers: { Accept: 'text/html' },
    query: { community_id: app.state.get(['community', 'id']) },
    body: { view, data, layout: true }
  });
}

export async function getDuetPreviewUrl(communityId, uuid) {
  const url = `/api/preview_url`;
  const result = await request(url, {
    query: {
      community_id: communityId,
      uuid
    }
  });
  return result.url;
}
