/**
 * performance marks are global
 * name of mark and GA label
 */
export const PERF_MARKS = {
  PREVIEW_CHANGE: 'preview tab change',
  PREVIEW_IFRAME: 'preview iframe',
  LAYOUT_IFRAME: 'layout iframe',
  CHROME: 'anthem chrome'
};

/**
 * Measure timing only once, starting at page load
 * var measure = require('../performance')('mylabel')
 * call measure() to measure and send
 * @param label {string} name of GA label
 * @param category {string} custom name for timingCategory (utc)
 */
export default function measureLoadingOnce(label, category) {
  var count = 0;
  return function() {
    if(!count && window.performance && window.ga) {
      sendTimingToGA(label, performance.now(), category);
      count++;
    }
  };
}

/**
 * Convienence method for adding marks
 * @param label {string} name of mark
 */
export function startMark(label) {
  if (window.performance && label) {
    performance.clearMarks(label);
    performance.mark(label);
  }
}

/**
 * Meaures time from a labelled mark or from page request (if no such mark exists)
 * and sends to google analytics
 * @param label {string} name of mark and GA label
 * @param gaLabel {string} custom label for GA (like for error cases)
 * @param category {string} custom name for timingCategory (utc)
 */
export function measureMark(label, gaLabel, category) {
  if (window.performance && window.ga && label) {
    var markName;
    var mark = performance.getEntriesByName(label).pop();
    if (mark) {
      markName = mark.name;
    }
    performance.measure(`${label}:measure`, markName);
    var measure = performance.getEntriesByName(`${label}:measure`).pop();
    var sendLabel = gaLabel ? gaLabel : label;
    sendTimingToGA(sendLabel, measure.duration, category);
    performance.clearMarks();
    performance.clearMeasures();
  }
}

/**
 * Meaures time from any mark or from page request (if defaultLabel is present AND no marks exist)
 * and sends to google analytics (using name of mark or defaultLabel)
 * if defaultLabel is not present and no marks exist, nothing is sent
 * @param defaultLabel {string} GA label if no marks exist
 * @param category {string} custom name for timingCategory (utc)
 */
export function measureAnyMark(defaultLabel, category) {
  if (window.performance && window.ga) {
    var mark, gaName, markName;
    mark = performance.getEntriesByType('mark').pop();
    if (mark) {
      markName = mark.name;
      gaName = markName;
    } else {
      gaName = defaultLabel;
    }
    if (gaName) {
      performance.measure(`${markName}:measure`, markName);
      var measure = performance.getEntriesByName(`${markName}:measure`).pop();
      sendTimingToGA(gaName, measure.duration, category);
    }
    performance.clearMarks();
    performance.clearMeasures();
  }
}

function sendTimingToGA(label, duration, category) {
  var obj = {
    hitType: 'timing',
    timingCategory: category,
    timingLabel: label,
    timingVar: 'load',
    timingValue: Math.round(duration)
  };
  window.ga('send', obj);
}
