var intersectRect = require('../intersect_rect');
var { isEqual } = require('lodash');
var { PERF_MARKS, measureMark } = require('../performance');

const MODIFY_BLOCKS = 'p,figure,aside,div:not(.m-ad)';

export default function insertModifyButtons(container) {
  placeModifySelectGroups(container.ownerDocument);
  if (app.state.get(['docLabel']) === 'package') {
    placePackageModifySelectGroups(container.ownerDocument);
  }
}

function placePackageModifySelectGroups(doc) {
  // We only want to add modify buttons to non-lead stories
  const packageCoverStories = Array.from(doc.querySelectorAll('[data-anthem-toggleable-layout=true]'));
  for (let node of packageCoverStories) {
    const index = parseInt(node.dataset.anthemLayoutIndex);
    const select = makeModifySelectGroup(node, { floated: [], hanging: [] }, { index }, false);
    node.insertBefore(select, node.firstChild);
  }
}

// ##### Object Modify Button Placement #####

function placeModifySelectGroups(doc) {
  // Defer inserting buttons to let other document markup render. This is to
  // ensure checks for adjacent elements use the final bound values for the nodes.
  setTimeout(() => {
    // Get all left-aligned nodes, separating into float and hang groups
    var floatedNodeBounds = Array.from(doc.querySelectorAll('.c-float-left')).reduce((result, node) => {
      if (node.classList.contains('c-float-hang')) {
        result.hanging.push(node.getBoundingClientRect());
      } else {
        result.floated.push(node.getBoundingClientRect());
      }

      return result;
    }, { floated: [], hanging: [] });

    var body = app.state.get(['doc', 'head', 'contents', 'draft', 'body']);
    for (let op of body.ops) {
      var id = op.attributes && op.attributes.id;
      if (id) {
        var node = doc.getElementById(id);
        if (node && node.matches(MODIFY_BLOCKS) && node.innerHTML) {
          var select = makeModifySelectGroup(node, floatedNodeBounds, { id: node.id });
          node.insertBefore(select, node.firstChild);
        }
      }
    }
    measureMark(PERF_MARKS.LAYOUT_IFRAME, PERF_MARKS.LAYOUT_IFRAME, 'u-Layout');
  });
}

function makeModifySelectGroup(node, floatedNodeBounds, data, hasCheckbox = true) {
  var activeIds = app.state.get(['layoutBlockIds']) || [];
  var isActiveBlock = node.id && activeIds.indexOf(node.id) > -1;

  // Create the container
  var container = document.createElement('span');
  container.dataset.data = JSON.stringify(data);
  container.classList.add('p-anthem-selection');

  // If the current block is wrapping a left aligned block, set a class that will
  // apply an offset to the modify button for the wrapped block so that it appears
  // in the proper position
  if (nodeWrapsFloatedBlock(floatedNodeBounds.hanging, node)) {
    container.classList.add('p-anthem-selection__right-hang');
  } else if (nodeWrapsFloatedBlock(floatedNodeBounds.floated, node)) {
    container.classList.add('p-anthem-selection__right');
  }

  // Add the button
  var button = document.createElement('button');
  button.type = 'button';
  button.textContent = 'Modify';
  button.classList.add('p-anthem-selection__button');
  button.onclick = handleModifyButtonClick;
  if (isActiveBlock) {
    button.classList.add('is-active');
  }
  container.appendChild(button);

  if (hasCheckbox) {
    // Add the multi-select checkbox: an <input> + a <b> wrapped in a <label> so
    // we can mock the checkbox styles
    var checkbox = document.createElement('input');
    checkbox.type = 'checkbox';
    checkbox.classList.add('p-anthem-selection__checkbox');
    checkbox.checked = isActiveBlock && activeIds.length > 1;
    checkbox.onchange = handleModifyCheckboxClick;

    var checkboxWrapper = document.createElement('label');
    checkboxWrapper.appendChild(checkbox);
    checkboxWrapper.appendChild(document.createElement('b'));
    container.appendChild(checkboxWrapper);
  }

  return container;
}

function nodeWrapsFloatedBlock(floatedBlockBounds, current) {
  // If the current node is a floated node, it will pass as an intersection but
  // that's not what we want, so force a `false`
  if (current.parentElement.classList.contains('c-float-left')) {
    return false;
  }

  var currentBounds = current.getBoundingClientRect();
  return floatedBlockBounds.some(n => intersectRect(n, currentBounds));
}

function handleModifyCheckboxClick(e) {
  var checkbox = e.target;
  var button = checkbox.parentNode.previousSibling;
  var activeBlockIds = [];

  // 1) Set sibling button as active if not already
  if (!button.classList.contains('is-active')) {
    button.classList.toggle('is-active');
  }

  // 2) Set all other non-checked-but-active buttons as inactive
  var selectGroups = [...checkbox.ownerDocument.querySelectorAll('.p-anthem-selection')];
  for (var group of selectGroups) {
    var groupCheckbox = group.querySelector('.p-anthem-selection__checkbox');
    if (!groupCheckbox || !groupCheckbox.checked) {
      group.firstChild.classList.remove('is-active');
    } else if (groupCheckbox && groupCheckbox.checked) {
      var groupData = JSON.parse(group.dataset.data);
      activeBlockIds.push(groupData.id);
    }
  }

  // 3) Push the array of ids for the block toolbar
  app.state.set(['layoutBlockIds'], activeBlockIds);
}

function handleModifyButtonClick(e) {
  var target = e.target;
  var parent = target.parentNode;
  var parentData = JSON.parse(parent.dataset.data);
  var siblingCheckbox = parent.querySelector('.p-anthem-selection__checkbox');
  var shouldClose = siblingCheckbox && !siblingCheckbox.checked;

  // 1) Set this group as selected if not already
  if (!target.classList.contains('is-active')) {
    target.classList.toggle('is-active');
  }

  // 2) Remove active state from others
  //    - Remove is-active from all other buttons
  //    - Uncheck all checkboxes, even sibling
  var selectGroups = [...target.ownerDocument.querySelectorAll('.p-anthem-selection')];
  for (var group of selectGroups) {
    var checkbox = group.querySelector('.p-anthem-selection__checkbox');
    if (checkbox) {
      checkbox.checked = false;
    }
    var groupData = JSON.parse(group.dataset.data);
    if (!isEqual(groupData, parentData)) {
      group.firstChild.classList.remove('is-active');
    }
  }

  // 3) Block toolbar logic:
  //    - Open or close the edit interface for the appropriate block.
  //    - Only close if active && the sibling checkbox is not the reason it's open
  if (parentData.id) {
    var layoutBlockIds = app.state.get(['layoutBlockIds']);
    if (layoutBlockIds && layoutBlockIds[0] === parentData.id && shouldClose) {
      app.state.set(['layoutBlockIds'], []);
    } else {
      app.state.set(['layoutBlockIds'], [parentData.id]);
    }
    app.state.set(['layoutBlockIndex'], null);
  } else if (parentData.index) {
    var layoutBlockIndex = app.state.get(['layoutBlockIndex']);
    if (layoutBlockIndex === parentData.index) {
      app.state.set(['layoutBlockIndex'], null);
    } else {
      app.state.set(['layoutBlockIndex'], parentData.index);
    }
    app.state.set(['layoutBlockIds'], []);
  }
}
